// Calculate top genres with half-life formula
export const calculateTopGenres = (tracks = [], artists = [], useHalfLife = true) => {
  const genreWeights = {};
  let totalWeight = 0;
  
  // Half-life weight function: w_i = e^(-0.01(i-1))
  const getWeight = (index) => {
    if (!useHalfLife) return 1;
    return Math.exp(-0.01 * index);
  };
  
  // Process artists with weighted scoring
  artists.forEach((artist, index) => {
    const weight = getWeight(index);
    
    if (artist.genres && Array.isArray(artist.genres)) {
      artist.genres.forEach(genre => {
        genreWeights[genre] = (genreWeights[genre] || 0) + weight;
        totalWeight += weight;
      });
    }
  });
  
  // Process track artists with weighted scoring
  // Note: Track artists usually don't include genre data directly,
  // but we process them in case the data structure includes it
  tracks.forEach((track, index) => {
    const weight = getWeight(index);
    
    if (track.artists && Array.isArray(track.artists)) {
      track.artists.forEach(artist => {
        if (artist.genres && Array.isArray(artist.genres)) {
          artist.genres.forEach(genre => {
            genreWeights[genre] = (genreWeights[genre] || 0) + weight;
            totalWeight += weight;
          });
        }
      });
    }
  });
  
  // Convert to array and calculate percentages
  const genreArray = Object.entries(genreWeights)
    .map(([name, weight]) => ({ 
      name, 
      weight,
      percentage: totalWeight > 0 ? (weight / totalWeight) * 100 : 0,
      trackCount: tracks.length,
      artistCount: artists.length
    }))
    .sort((a, b) => b.weight - a.weight);
  
  return genreArray;
};

// Decode HTML entities
export const decodeHtmlEntities = (text) => {
  if (!text) return '';
  
  const parser = new DOMParser();
  const decodedString = parser.parseFromString(text, 'text/html').documentElement.textContent;
  return decodedString || text;
};

// Format duration from milliseconds
export const formatDuration = (ms) => {
  const minutes = Math.floor(ms / 60000);
  const seconds = Math.floor((ms % 60000) / 1000);
  return `${minutes}:${seconds.toString().padStart(2, '0')}`;
};

// Format date
export const formatDate = (dateString) => {
  const date = new Date(dateString);
  const now = new Date();
  const diffMs = now - date;
  const diffMins = Math.floor(diffMs / 60000);
  const diffHours = Math.floor(diffMins / 60);
  const diffDays = Math.floor(diffHours / 24);
  
  if (diffMins < 1) return 'Just now';
  if (diffMins < 60) return `${diffMins} minute${diffMins === 1 ? '' : 's'} ago`;
  if (diffHours < 24) return `${diffHours} hour${diffHours === 1 ? '' : 's'} ago`;
  if (diffDays < 30) return `${diffDays} day${diffDays === 1 ? '' : 's'} ago`;
  
  return date.toLocaleDateString();
};

// Group items by property
export const groupBy = (items, key) => {
  return items.reduce((groups, item) => {
    const group = item[key];
    if (!groups[group]) groups[group] = [];
    groups[group].push(item);
    return groups;
  }, {});
};

// Extract unique items from recently played
export const extractUniqueFromRecentlyPlayed = (items = []) => {
  const artists = new Map();
  const albums = new Map();
  const podcasts = new Map();
  
  items.forEach(item => {
    const track = item.track;
    
    // Extract artists
    track.artists?.forEach(artist => {
      if (!artists.has(artist.id)) {
        artists.set(artist.id, {
          ...artist,
          playCount: 1,
          lastPlayed: item.played_at
        });
      } else {
        const existing = artists.get(artist.id);
        existing.playCount++;
        if (item.played_at > existing.lastPlayed) {
          existing.lastPlayed = item.played_at;
        }
      }
    });
    
    // Extract albums
    if (track.album && !albums.has(track.album.id)) {
      albums.set(track.album.id, {
        ...track.album,
        playCount: 1,
        lastPlayed: item.played_at
      });
    } else if (track.album) {
      const existing = albums.get(track.album.id);
      existing.playCount++;
      if (item.played_at > existing.lastPlayed) {
        existing.lastPlayed = item.played_at;
      }
    }
    
    // Extract podcasts (if track type is episode)
    if (track.type === 'episode' && track.show) {
      if (!podcasts.has(track.show.id)) {
        podcasts.set(track.show.id, {
          ...track.show,
          playCount: 1,
          lastPlayed: item.played_at
        });
      } else {
        const existing = podcasts.get(track.show.id);
        existing.playCount++;
        if (item.played_at > existing.lastPlayed) {
          existing.lastPlayed = item.played_at;
        }
      }
    }
  });
  
  return {
    artists: Array.from(artists.values()).sort((a, b) => b.playCount - a.playCount),
    albums: Array.from(albums.values()).sort((a, b) => b.playCount - a.playCount),
    podcasts: Array.from(podcasts.values()).sort((a, b) => b.playCount - a.playCount),
  };
};