import './ItemComponents.css';

const GenreItem = ({ genre, index }) => {
  if (!genre) return null;

  const percentage = Math.round(genre.percentage);
  const { trackCount = 500, artistCount = 500 } = genre; // Default to 500 if not provided

  return (
    <li className="genre-item">
      <div className="genre-header">
        <span className="item-index">{index}.</span>
        <span className="genre-name">{genre.name}</span>
        <span className="genre-counts">
          {trackCount}T / {artistCount}A
        </span>
      </div>
      <div className="genre-bar-container">
        <div 
          className="genre-bar"
          style={{ width: `${percentage}%` }}
        />
      </div>
    </li>
  );
};

export default GenreItem;